.header{
  background-color: #ccc;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
	position: relative;
	width: 100%;
}

h1{
	text-align: center;
	font-size: 90px;
	font-weight: bold;
	padding: 90px 0 20px 0; 
}

h2{
	text-align: center;
	font-size: 25px;
	padding-bottom: 40px;
}
.card {
  border: 8px solid black;
  height: 150px;
  width: 200px;
  box-shadow: 0 3px 6px rgba(0,0,0,0.22);
  margin: 15px 45px;
  background-color: gray;
}

.card .fa-spinner {
  font-size: 180px;
  position: absolute;
  left: 25%;
  top: 25%;
}

img {
  height: 125px;
  width: 150px;
  padding: 10px;
  margin-left: 9%;
  margin-top: 5%;
}
.footer {
  min-height: 100px;
  background: #E5E5E5;
  position: relative;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
}

.footer-text {
  padding: 10px;
  text-align: center;
}

.wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    padding: 0px 200px 50px 200px;
    -webkit-justify-content: space-around;
            justify-content: space-around;
  }

  @media only screen and (max-width: 600px) {
    .wrapper {
        padding: 0px 20px 50px 20px;
    }
  }
* {
  box-sizing: border-box;
}

html,
body,
#root,
#root > div,
div[data-reactroot] {
  height: 100%;
  padding: 0;
  margin: 0;
}

